.Parth4 {
  padding: 96px 0px 96px 0px;
}

@media (max-width: 680px) {
  .Parth4 {
    padding: 30px 0px 30px 0px;
  }
}

.parth4__columns {
  -webkit-column-gap: 30px;
          column-gap: 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

@media (max-width: 680px) {
  .parth4__columns {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
}

.parth4-columns__column {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 50%;
          flex: 1 1 50%;
  position: relative;
}

@media (max-width: 680px) {
  .parth4-columns__column {
    margin: 0px 0px 15px 0px;
  }
  .parth4-columns__column:last-child {
    margin: 0;
  }
}

.content-items-item__link {
  display: block;
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #ffff;
  border-radius: 4px;
}

.content-items-item__link:hover {
  width: 100%;
  text-align: center;
  border-radius: 0;
}

.parth4-column__img {
  position: relative;
  padding: 0px 0px 50% 0px;
}

.content-link__toptext {
  text-align: center;
  font-weight: 600;
  font-size: 12px;
  line-height: 13px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  opacity: 0.6;
  margin: 6px 0px 8px 0px;
}

.content-link__bottomtext {
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 18px;
  letter-spacing: 0.06em;
}

.content-link__bottomtext span {
  color: #e91e63;
  opacity: 1;
  font-weight: 600;
  font-size: 16px;
  line-height: 18px;
}

.parth4-column__img img {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
